<template>
  <div class="icons">
    <swiper :options="swiperOption">
      <swiper-slide
        v-for="(page, index) of pages"
        :key="index"
      >
        <div
          class="icons-item"
          v-for="item of page"
          :key="item.id"
        >
          <div class="icons-item-img-wrapper">
            <img class="icons-item-img" :src='item.imgUrl'>
          </div>
          <p class="icons-item-desc">{{item.desc}}</p>
        </div>
      </swiper-slide>
    </swiper>
  </div>
</template>

<script>
export default {
  name: 'Icons',
  data () {
    return {
      swiperOption: {
        autoplay: false
      }
    }
  },
  props: {
    list: Array
  },
  computed: {
    pages () {
      const pages = []
      this.list.map((item, index) => {
        const page = Math.floor(index / 8)
        if (!pages[page]) {
          pages[page] = []
        }
        pages[page].push(item)
      })
      return pages
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~assets/styles/varibles.styl'
@import '~assets/styles/mixins.styl'
.icons >>> .swiper-container
  height: 50vw
.icons
  overflow :hidden
  .icons-item
    position: relative
    float: left
    height: 25vw
    overflow: hidden
    width: 25%
    .icons-item-img-wrapper
      position: absolute
      top: 0
      left: 0
      right: 0
      bottom: .44rem
      box-sizing: border-box
      padding: .1rem
      .icons-item-img
        height: 100%
        display: block
        margin: 0 auto
    .icons-item-desc
      position: absolute
      left: 0
      right: 0
      bottom: 0
      line-height: .44rem
      text-align:center
      color: $darkTextColor
      ellipsis()
</style>



