<template>
  <div>
    <div class="search">
      <input
        class="search-input"
        type="text"
        v-model="keyword"
        placeholder="输入城市名或拼音">
    </div>
    <div
      ref="search"
      v-show="keyword"
      class="search-content">
      <ul>
        <li
          class="search-item border-bottom"
          v-for="item of list"
          :key="item.id"
          @click="handleCityClick(item.name)"
        >
          {{item.name}}
        </li>
        <li
          v-show="hasnodata"
          class="search-item border-bottom">
          没有找到匹配数据
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import Bscroll from 'better-scroll'
import { mapMutations } from 'vuex'
export default {
  name: 'CitySearch',
  props: {
    cities: {
      type: Object
    }
  },
  data () {
    return {
      keyword: '',
      list: [],
      timer: null
    }
  },
  computed: {
    hasnodata () {
      return !this.list.length
    }
  },
  watch: {
    keyword () {
      if (this.timer) {
        clearTimeout(this.timer)
      }
      if (!this.keyword) {
        this.list = []
        return
      }
      this.timer = setTimeout(() => {
        const result = []
        for (let i in this.cities) {
          this.cities[i].forEach((value) => {
            if (value.spell.indexOf(this.keyword) > -1 || value.name.indexOf(this.keyword) > -1) {
              result.push(value)
            }
          })
        }
        this.list = result
      }, 100)
    }
  },
  methods: {
    handleCityClick (city) {
      this.changeCity(city)
      this.$router.push({path: '/'})
    },
    ...mapMutations(['changeCity'])
  },
  mounted () {
    this.scroll = new Bscroll(this.$refs.search)
  }
}
</script>

<style lang="stylus" scoped>
  @import "~assets/styles/varibles.styl"
  .search
    height: .72rem
    padding: 0 .1rem
    background: $bgColor
    .search-input
      box-sizing: border-box
      width: 100%
      padding: 0 .1rem
      height: .62rem
      line-height: center
      text-align : center
      border-radius: .06rem
      color: #666
  .search-content
    position:absolute
    top: .72rem
    left: 0
    right: 0
    bottom: 0
    height:90vh
    z-index: 2
    overflow :hidden
    background:#eee
    .search-item
      padding-left: .2rem
      color: #666
      background:#fff
      text-align:left
      line-height: .62rem

</style>



