<template>
  <div class="list" ref="wrapper">
    <div>
      <section class="area">
        <div class="area-title border-topbottom">当前城市</div>
        <div class="button-list">
          <div class="button-list-item">
            <div class="button-list-item-content">{{this.currentCity}}</div>
          </div>
        </div>
      </section>
      <section class="area">
        <div class="area-title border-topbottom">
          热门城市
        </div>
        <div class="button-list">
          <div
            class="button-list-item"
            v-for="item of hotCities"
            @click="handleCityClick(item.name)"
            :key="item.id">
            <div class="button-list-item-content">{{item.name}}</div>
          </div>
        </div>
      </section>
      <section
        class="area"
        v-for="(item,key) of cities"
        :ref="key"
        :key="key">
        <div class="area-title border-topbottom">{{key}}</div>
        <div class="city-list">
          <div
            class="city-list-item"
            v-for="innerItem of item"
            :key="innerItem.id"
            @click="handleCityClick(innerItem.name)"
          >
            {{innerItem.name}}
          </div>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers, mapState } from 'vuex'
import Bscroll from 'better-scroll'
const { mapMutations } = createNamespacedHelpers('mod1')

export default {
  name: 'CityList',
  props: {
    hotCities: Array,
    cities: Object,
    letter: {
      type: String,
      default: 'A'
    }
  },
  watch: {
    letter () {
      if (this.letter) {
        const element = this.$refs[this.letter][0]
        this.scroll.scrollToElement(element)
      }
    }
  },
  methods: {
    handleCityClick (city) {
      this.changeCity(city)
      this.$router.push({path: '/'})
    },
    ...mapMutations(['changeCity'])
  },
  mounted () {
    this.scroll = new Bscroll(this.$refs.wrapper, {click: true})
  },
  computed: {
    ...mapState({
      currentCity: state => state.mod1.city
    })
  }
}
</script>

<style lang="stylus" scoped>
  @import '~assets/styles/varibles.styl'
.list
  overflow: hidden
  position: absolute
  top:1.58rem
  left:0
  right:0
  bottom:0
  .area
    &-title
      text-align: left
      line-height: .54rem
      background : #eee
      padding-left :.2rem
      color :#666
      font-size :.26rem
    .border-topbottom
      &:before
        border-color: #ccc
      &:after
        border-color: #ccc
    .border-topbottom
      &:before
        border-color: #ccc
    .button-list
      padding:.1rem .6rem .1rem .1rem
      overflow :hidden
      &-item
        float :left
        width:30%
        &-content
          border: .02rem solid #ccc
          margin: .1rem
          padding: .1rem 0
          border-radius: .06rem
          text-align :center
    .city-list
      &-item
        line-height:.54rem
        text-align:left
        padding-left:.2rem
</style>



