import Vue from 'vue'
import Router from 'vue-router'
import Home from 'pages/home/index'
import City from 'pages/city/index'
import Detail from 'pages/detail/index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    }, {
      path: '/city',
      name: 'City',
      component: City
    }, {
      path: '/Detail/:id',
      name: 'Detail',
      component: Detail
    }],
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})



