<template>
  <div>
    <div
      @click="handleBannerClick"
      class="banner">
      <img class="banner-img" :src="bannerImg">
      <div class="banner-info">
        <div class="banner-title">{{sightName}}</div>
        <div class="banner-number">
          <span class="iconfont banner-icon">&#xe692;</span>39
        </div>
      </div>
    </div>
    <div class="g-container-gallary">
      <fade>
        <common-gallary
          v-show="showGallary"
          :imgs="gallaryImgs"
          @closeGallary="closeGallary"
        />
      </fade>
    </div>
  </div>
</template>
<script>
import CommonGallary from 'base/gallary/index'
import fade from 'base/fade/index'
export default {
  name: 'DetailBannar',
  data () {
    return {
      showGallary: false
    }
  },
  props: {
    sightName: {
      type: String
    },
    bannerImg: {
      type: String
    },
    gallaryImgs: {
      type: Array
    }
  },
  methods: {
    handleBannerClick () {
      this.showGallary = true
    },
    closeGallary () {
      this.showGallary = false
    }
  },
  components: {CommonGallary, fade}
}
</script>
<style lang="stylus" scoped>
  .banner
    position :relative
    height:0
    padding-bottom :55%
    overflow:hidden
    .banner-img
      width:100%
    .banner-info
      position :absolute
      right:0
      bottom:0
      display flex
      line-height: .6rem
      left:0
      color :#fff
      background-image: linear-gradient(top, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.8))
      .banner-title
        flex:1
        font-size :.28rem
        padding: 0 .2rem
        text-align :left
      .banner-number
        margin-top:.14rem
        padding:0 .4rem
        height:.32rem
        border-radius : .2rem
        font-size :.24rem
        background : rgba(0, 0, 0, .8)
        line-height:.32rem
        .banner-icon
          font-size :.24rem
</style>



