<template>
<div>
  <div class="g-container-header">
    <city-header/>
  </div>
  <div class="g-container-search">
    <city-search :cities="cities"/>
  </div>
  <div class="g-container-list">
    <city-list
      :cities="cities"
      :hotCities="hotCities"
      :letter="letter"
    />
  </div>
  <div class="g-container-aphal">
    <city-aphal
      @change="handleLetterChange"
      :cities="cities"/>
  </div>
</div>
</template>

<script>
import CityHeader from './components/header'
import CitySearch from './components/search'
import CityList from './components/list'
import CityAphal from './components/aphal'
import axios from 'axios'
export default {
  name: 'City',
  components: {
    CityAphal,
    CityList,
    CityHeader,
    CitySearch
  },
  data () {
    return {
      cities: {},
      hotCities: [],
      letter: ''
    }
  },
  methods: {
    getCityInfo () {
      axios.get('/static/mock/city.json')
        .then(this.handleGetCityInfoSucc)
    },
    handleGetCityInfoSucc (res) {
      res = res.data
      if (res.ret && res.data) {
        const data = res.data
        this.cities = data.cities
        this.hotCities = data.hotCities
      }
    },
    handleLetterChange (letter) {
      this.letter = letter
    }
  },
  mounted () {
    this.getCityInfo()
  }
}
</script>

<style lang="stylus" scoped>
  @import '~assets/styles/varibles.styl'
  .g-container-header
    position:fixed
    top:0
    width:100%
    z-index :2
  .g-container-search
    width: 100%
    position:fixed
    z-index :2
    top:$headerHeight
  .g-container-list
    width: 100%
    margin-top: -.14rem
</style>



