<template>
  <div class="header">
    <div class="header-left">
      <div class="iconfont back-icon">&#xe624;</div>
    </div>
    <div class="header-input">
      <span class="iconfont">&#xe632;</span>
      输入城市/景点/游玩主题
    </div>
    <router-link to="/city">
      <div class="header-right">
        {{this.currentCity}}
        <span class="iconfont arrow-icon">&#xe64a;</span>
      </div>
    </router-link>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  name: 'Header',
  computed: {
    ...mapState({
      currentCity: state => state.mod1.city
    })
  }
}
</script>
<!--/*原始尺寸是86px，由于是两倍图的设计图，所以这里应该是43px 由于rem为50px，所以具体尺寸是.86rem*/-->
<style scoped lang="stylus">
  @import '~assets/styles/varibles.styl'
  .header
    display: flex
    line-height: .86rem
    background: $bgColor
    color: #fff
    padding-right: .2rem
    .header-left
      width: .64rem
      float: left
      .back-icon
        text-align: center
        font-size :.4rem
        color: #fff
    .header-input
      flex :1
      height :.64rem
      line-height :.64rem
      margin :.12rem .2rem
      text-align :left
      padding-left :.2rem
      background: #fff
      border-radius : .1rem
      color: #ccc
    .header-right
      min-width:1.04rem
      padding:0 .1rem
      float: right
      text-align:center
      color: #fff
      .arrow-icon
        margin-left: -.04rem
        font-size: .24rem
</style>



