<template>
  <div>
    <div class="g-container-bannar">
      <detail-bannar
        :sightName="sightName"
        :bannerImg="bannerImg"
        :gallaryImgs="gallaryImgs"
      />
    </div>
    <div class="g-container-head">
      <detail-head/>
    </div>
    <div class="g-container-list">
      <detail-list :list="categoryList"/>
    </div>
  </div>
</template>

<script>
import DetailBannar from './components/bannar'
import DetailHead from './components/head'
import DetailList from './components/list'
import axios from 'axios'
export default {
  name: 'Detail',
  data () {
    return {
      sightName: '',
      bannerImg: '',
      gallaryImgs: '',
      categoryList: ''
    }
  },
  components: {DetailList, DetailHead, DetailBannar},
  methods: {
    getDetailInfo () {
      axios.get('/static/mock/detail.json', {
        params: {
          id: this.$route.params.id
        }
      }).then(this.handleGetDataSucc)
    },
    handleGetDataSucc (res) {
      res = res.data
      if (res.ret && res.data) {
        const data = res.data
        this.sightName = data.sightName
        this.bannerImg = data.bannerImg
        this.gallaryImgs = data.gallaryImgs
        this.categoryList = data.categoryList
      }
    }
  },
  mounted () {
    this.getDetailInfo()
  },
  activated () {
    this.getDetailInfo()
  }
}
</script>
<style lang="stylus" scoped>
.g-container-bannar
  position :absolute
  top:0
.g-container-head
  text-align: left
.g-container-list
  position:absolute
  top:55vw
  height:50rem
</style>



